* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, sans-serif;
}

.search-form {
  background-color: #333;
  justify-content: center;
  gap: 10px;
  padding: 20px;
  display: flex;
}

.search-form input {
  width: 70%;
  padding: 10px;
}

.search-form button {
  cursor: pointer;
  padding: 10px 20px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  margin: 20px;
  display: grid;
}

.load-more {
  cursor: pointer;
  margin: 20px auto;
  padding: 10px 20px;
  display: block;
}

